body{
  background: $body-color;
}

._100vh{
  height: 100vh;
}

.section{
  padding: 100px 0;
}

.section__title {
  position: relative;
  margin-bottom: 150px;
  z-index: 1;
  &::before{
    content: attr(data-content);
    font-family: $primary-font;
    font-size: 100px;
    line-height: 100px;
    color: rgb(8, 8, 8);
    opacity: 0.07;
    position: absolute;
    z-index: -1;
    left: 50%;
    bottom: 0px;
    transform: translateX(-50%);
    white-space: nowrap;
  }
  &::after{
    content: '';
    position: absolute;
    width: 160px; height: 4px;
    background: linear-gradient( 0deg, rgb(62,27,189) 0%, rgb(160,97,255) 100%);
    border-radius: 4px;
    left: 50%; transform: translateX(-50%);
    bottom: -20px;
  }
  &.white{
    color: $white;
    &::before{
      color: $white;
    }
    &::after{
      background: #f5f7f9;
      opacity: .33;
    }
  }
  &.left{
    margin-bottom: 100px;
    &:before, &:after{
      left: 0; transform: translateX(0);
    }
  }
}