@import url('https://fonts.googleapis.com/css?family=Arimo:400,700|Cuprum:400,700');

body{
  font-family: $secondary-font;
}

p, span{
  font-family: $secondary-font;
}

h1, h2, h3, h4, h5, h6{
  font-family: $primary-font;
}

h1, .h1{
  font-size: 60px;
}

h2, .h2{
  font-size: 50px;
}

h3, .h3{
  font-size: 30px;
}

h4, .h4{
  font-size: 20px;
}

h5, .h5{
  font-size: 18px;
}