.process__item {
  display: flex;
  box-shadow: rgba(25, 28, 33, 0.1) 0px 85px 98px 0px;
  margin-bottom: 140px;
  width: 80%;
  position: relative;
  z-index: 1;
  .image {
    flex-basis: 48%;
    position: relative;
    // Background Styles
    .bg__shape-one {
      position: absolute;
      width: 90%;
      bottom: 0px;
      right: 2%;
      opacity: .54;
      img {
        width: 100%;
      }
    }
    .bg__shape-two{
      position: absolute;
      width: 100%;
      top: -40px;
      left: -20px;
      opacity: 0.58;
      img{
        width: 100%;
      }
    }
    .bg__shape-three{
      position: absolute;
      width: 95%;
      opacity: .48;
      bottom: 10%;
      left: 2%;
      img{
        width: 100%;
      }
    }
    // Middle Illlustration Styles
    .illustration__middle-one{
      position: absolute;
      width: 300px;
      z-index: 1;
      top: 50%; left: 50%;
      transform: translate(-70%, -53%);
    }
    .illustration__middle-two{
      position: absolute;
      width: 260px;
      z-index: 1;
      top: 50%; left: 50%;
      transform: translate(-50%, -50%)
    }
    .illustration__middle-three{
      position: absolute;
      width: 290px;
      z-index: 1;
      top: 50%; left: 50%;
      transform: translate(-50%, -50%)
    }
    // Right Illstration Styles
    .illustration__right-one{
      position: absolute;
      right: -2%;
      width: 160px;
      bottom: 0;
      z-index: 1;
    }
    .illustration__right-two{
      position: absolute;
      right: 0; bottom: 0;
      border-radius: 0 0 8px 0;
    }
    .illustration__right-three{
      position: absolute;
      right: -10%; bottom: 0;
      width: 140px;
      z-index: 1;
    }
    // Left Illustration Styles
    .illustration__left-three{
      position: absolute;
      transform: scaleX(-1);
      z-index: 1;
      bottom: 0; border-radius: 0 0 8px 0;
    }
  }
  .content {
    flex-basis: 52%;
    background: rgb(255, 255, 255);
    padding: 110px 60px;
    h2 {
      margin-bottom: 50px;
      position: relative;
      &::before {
        content: attr(data-content);
        position: absolute;
        font-size: 90px;
        opacity: 0.07;
        bottom: -10px;
        display: block;
        white-space: nowrap;
      }
    }
    p {
      margin-bottom: 0px;
      font-size: 20px;
      line-height: 30px;
    }
  }
  &:nth-child(odd){
    .image {
      background: linear-gradient(127deg, rgb(244, 61, 155) 0%, rgb(255, 142, 127) 100%);
      border-radius: 8px 0px 0px 8px;
    }
    .content {
      border-radius: 0px 8px 8px 0px;
    }
  }
  &:nth-child(even){
    margin-left: auto;
    .image {
      order: 2;
      background: linear-gradient(127deg, rgb(62, 27, 189) 0%, rgb(160, 97, 255) 100%);
      border-radius: 0px 8px 8px 0px;
    }
    .content{
      border-radius: 8px 0px 0px 8px;
    }
  }
  &:nth-child(1){
    &::before{
      content: '';
      position: absolute;
      width: 50%; height: 60%;
      border-width: 1px 1px 1px 0;
      border-style: dashed;
      border-color: $border-color;
      right: -15%;
      z-index: -1;
      bottom: -70px;
    }
  }
  &:nth-child(2){
    &::before{
      content: '';
      position: absolute;
      width: 1px;
      height: calc(100% + 140px);
      border-width: 0 0 0 1px;
      border-style: dashed;
      border-color: $border-color;
      top: -70px;
      z-index: -1;
      left: 40%;
    }
  }
  &:nth-child(3){
    &::before{
      content: '';
      position: absolute;
      width: 50%; height: 60%;
      border-width: 1px 1px 1px 0;
      border-style: dashed;
      border-color: $border-color;
      right: -15%;
      z-index: -1;
      top: -70px;
    }
  }
}