.footer{
  padding: 300px 0 0;
  background: linear-gradient( 127deg, rgb(255,142,127) 0%, rgb(244,61,155) 100%);
  position: relative;
  overflow: hidden;
  &__shape{
    &-bg{
      position: absolute;
      width: 100%;
      top: -1px;
      img{
        width: 100%;
      }
    }
    &-bottomleft{
      position: absolute;
      left: -2%;
      bottom: -20%;
      width: 45%;
      img{
        width: 100%;
      }
    }
  }
  &__content{
    .section__title{
      &::after{
        content: none;
      }
      margin-bottom: 40px;
    }
    p{
      width: 70%;
      margin: 0 auto;
      color: $white;
      margin-bottom: 80px;
    }
    .btn-white{
      font-size: 18px;
      font-weight: 700;
      letter-spacing: 1px;
      i{
        display: inline-block;
        font-size: 30px;
        transform: translateY(4px);
        margin-left: 25px;
        background-image: linear-gradient( -53deg, rgb(233,83,122) 0%, rgb(255,150,136) 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent
      }
    }
    margin-bottom: 100px;
  }
  &__bottom{
    padding: 20px 0 15px;
    border-top: 1px solid #fc9db0;
    .copyright{
      color: $white;
      margin-bottom: 0;
      font-size: 17px;
      font-family: $primary-font;
    }
    .footer__list{
      margin-bottom: 0;
      text-align: right;
      li.list-inline-item{
        &:not(:last-child){
          margin-right: 15px;
        }
        a{
          color: $white;
          font-size: 17px;
          font-family: $primary-font;
        }
      }
    }
  }
}