.main-nav{
  padding: 25px 30px;
  @include desktop{
    background: $primary-color;
    box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.09);
    padding: 20px;
  }
  .navbar-nav{
    .nav-item{
      .nav-link{
        color: $white;
        padding-left: 20px;
        padding-right: 20px;
        font-size: 15px;
        text-transform: capitalize;
        letter-spacing: 1px;
        position: relative;
        z-index: 1;
        font-family: $primary-font;
        position: relative;
        @include desktop{
          padding: 15px 0;
        }
        &:before{
          position: absolute;
          content: '';
          width: 20px;
          height: 3px;
          z-index: -1;
          background: $white;
          left: 21px;
          bottom: 5px;
          border-radius: 3px;
          transform: scaleX(0);
          transform-origin: right;
          transition: transform .3s ease;
          @include desktop{
            content: none;
          }
        }
        &:hover{
          &:before{
            transform: scaleX(1);
            transform-origin: left;
          }
        }
        &.active{
          &:before{
            transform: scaleX(1);
            transform-origin: left;
          }
        }
      }
    }
    @include desktop{
      margin-bottom: 20px;
    }
  }
  .nav-signin{
    margin-left: 20px;
  }
  .navbar-brand{
    margin-right: 0;
    text-align: center;
    img{
      max-width: 150px;
    }
  }
  .navbar-toggler{
    outline: 0;
    color: $white;
    cursor: pointer;
  }
  &.nav-bg{
    background: $primary-color;
    padding: 20px;
    border: none;
    box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.09);
    @include desktop{
      padding: 15px 20px;
    }
  }
}

// Headroomjs Styles
.headroom {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transition: all .5s ease-in-out;
}
.headroom--unpinned {
  top: -100px !important;
}
.headroom--pinned {
  top: 0;
}