.feature{
  background: url('../images/feature-bg.png');
  background-size: cover;
  background-position: top center;
  padding: 300px 0 100px;
  &__item{
    margin-bottom: 85px;
    &-header{
      display: flex;
      align-items: center;
      .icon{
        position: relative;
        width: 67px; height: 67px;
        background: linear-gradient( 127deg, rgb(244,61,155) 0%, rgb(255,142,127) 100%);
        border-radius: 8px;
        margin-right: 20px;
        img{
          position: absolute;
          width: 30px;
          left: 50%; top: 50%;
          transform: translate(-50%, -50%);
        }
      }
      margin-bottom: 25px;
    }
    h4,p{
      color: $white;
    }
    p{
      font-size: 15px;
      line-height: 25px;
    }
  }
}